export default {
  name: "shopCard",
  props: {
    shopObj: {
      type: Object
    },
    index: {
      type: Number
    }
  },
  data() {
    return {
      starColor: ["#FFD282",'#FFD282','#FFD282'],
      is_favorite: 1, //是否收藏 0代表未收藏 1代表已收藏
      heratImg: [
        require("../../static/images/icon/red_heart.png"),
        require("../../static/images/icon/close_heart.png")
      ],
    }
  },
  watch: {
    shopObj: {
      handler(value) {
        this.is_favorite = 1;
      },
      deep: true
    }
  },
  methods: {
    toShopDetail(id, name) {
      this.buryingPoint.productClick(id, name);
      this.$router.push({
        path: "/product/" + id
      });
    },
    //添加至心愿单
    addToWishList(goods_id){
      if(!this.$store.getters.getLoginToken){
        this.$router.push({
          path: "/login"
        });
        return;
      }
      let obj = {
        goods_id
      }
      this.$apis
        .favorite(obj)
        .then(res => {
          if (res.status == 1) {
            if(res.data.item){
              this.is_favorite = 1;
            }else{
              this.is_favorite = 0;
            }
          } else {
            this.common.msgBox(this, res.msg, 3);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
  }
};
